<template>
  <div class="w-full border-b hover:bg-lightest flex flex-col">
    <!-- Retweet header -->
    <div class="pt-4 pl-4 flex flex-row">
      <div class="w-12 mr-4 flex justify-end">
        <i class="text-sm pt-1 fas fa-retweet text-dark"></i>
      </div>
      <p class="text-sm text-dark">{{ label }}</p>
    </div>

    <Tweet :tweet="tweet" />
  </div>
</template>

<script>
import Tweet from "./Tweet.vue";
import {mapActions} from "vuex";

export default {
  name: "Retweet",
  props: {
    tweet: {
      type: Object,
      required: true
    }
  },
  components: {
    Tweet,
  },
  methods: {
    ...mapActions("warpnet", [
      "getOwnerProfile"
    ]),
  },
  data() {
    return {
      label: "",
      profile: {}
    };
  },
  async created() {
    try {
      this.profile = await this.getOwnerProfile();
      if (this.profile.id === this.tweet.retweeted_by) {
        this.label = "You Retweeted";
      } else {
        this.label = `${this.tweet.profile.username} Retweeted`;
      }
    } catch (error) {
      console.error('Error fetching profile:', error);
    }
  },
};
</script>
